<template>
    <div>
        <table-component
                ref="table"
                @handleOperate="handleOperate"
                :columns="columns"
                :request="tableRequest">
            <template slot="top">
                <a-button type="primary"
                          @click="handleCreate()">新增
                </a-button>
                <div style="height: 10px"></div>
            </template>
        </table-component>
        <a-modal
                width="40%"
                :title="modalTitle"
                v-model="modalVisible"
                :footer="null"
                :destroyOnClose="true">
            <form-component :id="formId"/>
        </a-modal>
    </div>
</template>

<script>
    import {getExamPaperList, deleteExamPaper} from '@/api/examPaper'
    import tableComponent from '@/components/table'
    import FormComponent from './components/Form'

    export default {
        components: {
            tableComponent, FormComponent
        },
        data() {
            return {
                modalTitle: null,
                modalVisible: false,
                formId: 0,
                columns: [
                    {
                        title: '#',
                        dataIndex: 'index',
                        scopedSlots: {customRender: 'index'},
                    },
                    {
                        title: '名称',
                        dataIndex: 'name',
                        key: 'name',
                    },
                    {
                        title: '答题时间',
                        dataIndex: 'duration',
                        key: 'duration',
                    },
                    {
                        title: '是否关联用户',
                        dataIndex: 'is_restrict_user',
                        key: 'is_restrict_user',
                    },
                    {
                        title: '满分',
                        dataIndex: 'max_score',
                        key: 'max_score',
                    },
                    {
                        title: '通过分数',
                        dataIndex: 'pass_score',
                        key: 'pass_score',
                    },
                    {
                        title: '题目数量',
                        dataIndex: 'questions_count',
                        key: 'questions_count',
                    },
                    {
                        title: '开考时间',
                        dataIndex: 'published_at_format',
                        key: 'published_at_format',
                    },

                    {
                        title: '操作',
                        dataIndex: 'operation',
                        scopedSlots: {customRender: 'operation'},
                        buttons: {
                            check: {visible: true, disable: false,},
                            edit: {visible: true, disable: false,},
                            delete: {visible: true, disable: false,},
                        }
                    }
                ],
                tableRequest: getExamPaperList,
            }
        },
        methods: {
            handleSearch(filter) {
                this.$refs.table.handleSearch(filter)
            },
            handleCreate() {
                this.modalTitle = '新增分类';
                this.modalVisible = true;
                this.formId = null;
            },
            handleEdit(id) {
                this.modalTitle = '编辑分类';
                this.modalVisible = true;
                this.formId = id;
            },
            handleOperate(type, row) {
                switch (type) {
                    case 'create':
                        this.handleCreate();
                        break;
                    case 'edit':
                        this.handleEdit(row.id);
                        break;
                    case 'delete':
                        deleteExamPaper(row.id)
                            .then(res => {
                                this.$message.success('删除成功');
                                this.$refs.table.reFetch();
                            });
                        break;
                }
            },
        },
    }
</script>

