<template>
    <a-form @submit="handleSubmit"
            :autoFormCreate="(form)=>{this.form = form}">
        <a-form-item
                label='出题的用户'
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 18 }"
                fieldDecoratorId="initiate_user_id"
        >
            <SelectUser/>
        </a-form-item>
        <a-form-item
                label='试卷标题'
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 18 }"
                fieldDecoratorId="name"
        >
            <a-input/>
        </a-form-item>
        <a-form-item
                label='作答时间'
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 18 }"
                fieldDecoratorId="duration"
        >
            <a-input-number :min="1"/>
            分钟
        </a-form-item>
        <a-form-item
                label='是否关联用户'
                :labelCol="{span: 5}"
                :wrapperCol="{span:18}"
                fieldDecoratorId="is_restrict_user"
                :fieldDecoratorOptions="{valuePropName:'checked'}"
        >
            <a-checkbox @change="(val)=>{this.is_restrict_user=!!val.target.checked;}"/>
        </a-form-item>
        <a-form-item
                v-show="is_restrict_user"
                label='关联的用户'
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 18 }"
                fieldDecoratorId="user_ids"
        >
            <SelectUsers/>
        </a-form-item>
        <a-form-item
                label='满分'
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 18 }"
                fieldDecoratorId="max_score"
        >
            <a-input-number :min="1"/>
            分
        </a-form-item>
        <a-form-item
                label='通过分'
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 18 }"
                fieldDecoratorId="pass_score"
        >
            <a-input-number :min="1"/>
            分
        </a-form-item>
        <a-form-item
                label='考题分类'
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 18 }"
                fieldDecoratorId="exam_category_id"
        >
            <SelectExamCategory/>
        </a-form-item>
        <a-form-item
                label='题目数量'
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 18 }"
                fieldDecoratorId="questions_count"
        >
            <a-input-number :min="1"/>
            题
        </a-form-item>
        <a-form-item
                label='每题分数'
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 18 }"
                fieldDecoratorId="per_question_score"
        >
            <a-input-number :min="1"/>
            分
        </a-form-item>
        <a-form-item
                label='开始答题日期'
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 18 }"
                fieldDecoratorId="published_at"
        >
            <DateTime/>
        </a-form-item>
        <a-form-item
                label='结束答题日期'
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 18 }"
                fieldDecoratorId="finished_at"
        >
            <DateTime/>
        </a-form-item>
        <a-form-item
                label='缩略图'
                :labelCol="{ span: 5 }"
                :wrapperCol="{ span: 18 }"
                fieldDecoratorId="more_thumbnail"
        >
            <upload-thumbnail/>
        </a-form-item>
        <a-form-item
                :wrapperCol="{ span: 12, offset: 5 }"
        >
            <a-button type='primary' htmlType='submit'>
                提交
            </a-button>
        </a-form-item>
    </a-form>
</template>

<script>
    import {getExamPaper, createExamPaper, updateExamPaper} from '@/api/examPaper'
    import SelectUser from '@/components/select/SelectUser'
    import SelectExamCategory from '@/components/select/SelectExamCategory'
    import SelectUsers from '@/components/select/SelectUsers'
    import DateTime from '@/components/select/DateTime'
    import uploadThumbnail from '@/components/upload/UploadThumbnail'

    export default {
        components: {
            SelectExamCategory, SelectUser, SelectUsers, DateTime, uploadThumbnail
        },
        props: {
            id: {
                type: Number,
                default: 0,
            }
        },
        data() {
            return {
                form: {},
                updateUploadThumbnailInitUrl: null,
                is_restrict_user: false,
                log: console.log
            }
        },
        computed: {},
        methods: {
            init() {
                if (!this.id) {
                    return;
                }

                getExamPaper(this.id)
                    .then(res => {
                        this.form.setFieldsValue({
                            initiate_user_id: res.data.initiate_user_id,
                            duration: res.data.duration,
                            exam_category_id: res.data.exam_category_id,
                            is_restrict_user: res.data.is_restrict_user,
                            max_score: res.data.max_score,
                            name: res.data.name,
                            pass_score: res.data.pass_score,
                            per_question_score: res.data.per_question_score,
                            questions_count: res.data.questions_count,
                            user_ids: res.data.user_ids,
                            finished_at: res.data.finished_at,
                            published_at: res.data.published_at,
                        })
                    })
            },
            beforeSubmit() {
                return true;
                let res = false
                // this.form.validateFields(
                //     (err) => {
                //         if (!err) {
                //             res = true
                //         }
                //     })
                return res
            },
            handleSubmit(e) {
                e.preventDefault();

                let request;

                let data = this.form.getFieldsValue();
                console.log(this.form);

                //

                if (this.id) {
                    request = updateExamPaper(this.id, data)
                } else {
                    request = createExamPaper(data)
                }
                request.then(() => {
                    let msg = this.id ? '更新成功' : '新增成功';
                    this.$message.success(msg);
                })
            },
        },
        mounted() {
            this.init();
        },
    }
</script>
